            answerBadge.textContent = "ÌïÑÏàò ÏûÖÎ†•";
            answerBadge.className = "field-badge required";
            queryHelp.innerHTML = "ü§ñ <strong>ÎãµÎ≥Ä Í∏∞Î∞òÏúºÎ°ú ÏûêÎèô ÏÉùÏÑ±Îê©ÎãàÎã§</strong>";
            answerHelp.innerHTML = "‚úÖ <strong>ÎãµÎ≥ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî</strong> (Ïù¥ ÎÇ¥Ïö©ÏúºÎ°ú ÏßàÎ¨∏ ÏÉùÏÑ±)";
            queryField.placeholder = "Ïó¨Í∏∞Ïóê ÏÉùÏÑ±Îêú ÏßàÎ¨∏Ïù¥ ÌëúÏãúÎê©ÎãàÎã§";
