            answerHelp.innerHTML = "ü§ñ <strong>ÏßàÎ¨∏ Í∏∞Î∞òÏúºÎ°ú ÏûêÎèô ÏÉùÏÑ±Îê©ÎãàÎã§</strong>";
            queryField.placeholder = "ÎãµÎ≥ÄÏùÑ ÏÉùÏÑ±Ìï† ÏßàÎ¨∏ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî";
            answerField.placeholder = "Ïó¨Í∏∞Ïóê ÏÉùÏÑ±Îêú ÎãµÎ≥ÄÏù¥ ÌëúÏãúÎê©ÎãàÎã§";
